import {
    PopoverHeader,
  } from "@chakra-ui/react";
import { nanoid } from "nanoid";
import PopoverComponent from "../PopoverComponent";
  
const SizesFilter = () => {

    const { root, font } = useStyles();

    const allSizes = ["4", "5", "6", "7", "7.5", "8", "8.5", "9", "10"];

  return (
    <PopoverComponent label="Size">
    {allSizes?.map((e, i) => (
      <PopoverHeader {...font} key={nanoid()}>
        {e}
      </PopoverHeader>
    ))}
  </PopoverComponent>
  )
}

export default SizesFilter

